.hide-component{
    display: none;
}

.listed-elements-bullets li{
    cursor: pointer;
}

button#add-appointment{
    width: 100%;
}

button#go-to-appointment{
    width: 100%;
}

.listed-elements-bullets li.selected:before {
  content: "•";
  font-size: 40px;
  height: 16px;
  width: 16px;
  margin-right: 10px;
  vertical-align: bottom;
  border: 1px solid #979797;
  color: #03919E;
  border-radius: 4px;
  display: inline-block;
  line-height: 19px;
  text-align: center; }


.listed-elements-bullets li.unselected:before {
  content: "•";
  font-size: 40px;
  height: 16px;
  width: 16px;
  margin-right: 10px;
  vertical-align: bottom;
  border: 1px solid #979797;
  color: #000000;
  border-radius: 4px;
  display: inline-block;
  line-height: 19px;
  text-align: center; }


